.card-easy-2 {
  padding: 14px 20px 14px 50px;

  &__container {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 120px;
    line-height: 108%;
    letter-spacing: 0.02em;
    color: var(--primary);
  }

  &__text {
    width: 100%;
    max-width: 420px;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 127%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    & > b {
      font-weight: 900;
    }

    & > span {
      font-weight: 900;
      color: var(--primary);
    }
  }
}
