.breadcrumbs {
  width: 100%;

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    row-gap: 10px;

    min-width: min-content;
    width: 100%;
    max-width: max-content;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    padding-right: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--light-blue-50p);

    min-width: min-content;
    width: max-content;

    font-size: 18px;
    color: var(--text);

    &:not(:first-child)::before {
      content: "";
      display: inline-block;
      background-image: url("../images/icons/crop.svg");
      background-repeat: no-repeat;
      background-size: contain;

      min-height: 12px;
      min-width: 8px;
      margin-right: 10px;
    }

    &:last-child {
      color: var(--gray);
      padding-right: 0;
    }

    transition: all var(--transition-time);

    @include onLabtop {
      &:not(:last-child):hover {
        transform-origin: left;
        transform: scale(1.05);
        color: var(--light-blue);
      }
    }
  }

  &__link {
    display: flex;
    flex-wrap: wrap;
    gap: 4px;
  }

  @include toLabtop {
    &__list {
      // width: min-content;
    }
  }

  @include toPhone {
    &__item {
      max-width: 220px;
    }

    &__link {
      font-size: 16px;
    }
  }
}
