.to-up {
  @include flex-center;
  @include size(60px);

  padding: 10px;
  transition: all 0.2s;
  opacity: 0.3;
  border: 1px solid transparent;
  border-radius: 100%;

  &__image {
    height: 100%;
  }

  @include onLabtop {
    &:hover {
      border: 1px solid var(--primary-50p);
      opacity: 1;
    }

    &:active {
      transform: rotateX(30deg);
      filter: drop-shadow(0 30px 10px var(--dark-blue));
    }
  }

  &--hidden {
    @include no-event;
    opacity: 0;
    transform: translateX(20px) scale(0.9);
  }

  @include toLabtop {
    @include size(46px);
    padding: 4px;
  }
}
