.short-contacts {
  $origin: &;
  text-align: end;
  margin-top: 23px;

  &__links {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__link {
    display: flex;
    justify-content: start;
    align-items: center;
    font-family: var(--font-family);
    color: var(--text-talee-dark);
    gap: 6px;
    margin-bottom: 10px;

    &:hover {
      text-decoration: underline;
    }

    & > *:link {
      font-size: clm_px(14, map-get($breakpoints, tablet), 20);
      color: var(--text-talee-dark);
    }
    
    &--bold {
      font-weight: 500;
      font-size: 23px;
      letter-spacing: 0.01em;
      color: var(--text-talee-dark);
    }
  }

  @include toLabtop {
    #{$origin}__links {
      flex-direction: column;
    }
  }

  @include toPhone {
    #{$origin}__links {
      flex-direction: column-reverse;
    }

    #{$origin}__link {
      & > *:link {
        font-size: clm_px(16, map-get($breakpoints, phone), 20);
      }
    }
  }
}
