.link {
  $origin: &;
  display: inline-flex;
  align-items: center;
  cursor: pointer;

  &__text {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: var(--text-tale-title);
    transition: all var(--transition-time);
    
    width: 100%;
    
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background-color: var(--text-tale-title-50);
      margin-top: 4px;
      transition: all var(--transition-time);
    }
  }

  &__icon {
    place-self: start;
    margin-top: 1px;
    margin-left: 6px;
    transition: all var(--transition-time);

    & svg {
      @include size(12px);
    }

    & > * * {
      stroke: var(--text-tale-title);
      transition: all var(--transition-time);
    }
  }

  @include hover {
    #{$origin}__text {
      color: var(--primary);

      &::after {
        background-color: var(--primary-50p);
      }
    }

    #{$origin}__icon > * * {
      stroke: var(--primary);
    }
  }

  // Effects

  &--light {
    #{$origin}__text {
      color: var(--surface-white-80);

      &::after {
        background-color: var(--surface-white-30);
      }
    }

    #{$origin}__icon {
      & > * * {
        stroke: var(--surface-white-80);
        transition: all var(--transition-time);
      }
    }

    @include hover {
      #{$origin}__text {
        color: var(--surface-white-80);
        text-shadow: 0 0 10px var(--surface-white-80);
        
        &::after {
          width: 58%;
          background-color: var(--surface-white-80);
        }
      }

      #{$origin}__icon > * * {
        stroke: var(--surface-white-80);
      }
    } 
  }

  &--negative {
    #{$origin}__text {
      color: var(--primary);

      &::after {
        background-color: var(--primary-50p);
      }
    }

    #{$origin}__icon {
      & > * * {
        stroke: var(--primary);
      }
    }

    &:hover {
      #{$origin}__text {
        color: var(--text-tale-title);

        &::after {
          background-color: var(--text-tale-title-50);
        }
      }

      #{$origin}__icon {
        & > * * {
          stroke: var(--text-tale-title);
        }
      }
    }
  }

  &--nowrap {
    text-wrap: nowrap;
  }
}
