.section-block {
  $origin: &;
  position: relative;

  &__title-container {
    display: flex;
    justify-content: space-between;
    align-items: start;
    margin-bottom: 60px;
  }

  &__content-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  &--background-1 {
    padding: 54px 30px;
    border-radius: 30px;
    background-image: url("../images/decor/decor_1.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 1;
  }

  &--background-2 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px;
    height: 750px;
    padding-top: 60px;
    border-radius: 30px;
    background-image: url("../images/decor/decor_7.jpg");
    z-index: 1;
  }

  &--columns-2 {
    #{$origin}__content-container {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &--columns-4 {
    #{$origin}__content-container {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &--slider-container {
    #{$origin}__content-container {
      display: block;
      width: 100%;
      max-width: 1234px;
      margin: auto;
    }
  }
}
