.modal {
  overflow: hidden;
  position: relative;
  margin: auto;

  border-radius: 30px;
  padding: 64px;
  width: 100%;
  max-width: 1234px;

  box-shadow: 0 0 14px 0 rgba(180, 174, 167, 0.19);
  background: var(--primary-surface-white);

  &__button {
    position: absolute;
    top: 0;
    right: 0;
    margin: 30px;
  }

  &__content {
    position: relative;
    z-index: 1;
  }
}
