.card-block-2 {
  $origin: &;

  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  gap: 15px;

  padding: 24px 28px;
  border: 2px solid var(--surface-white-30);
  border-radius: 12px;

  -webkit-backdrop-filter: blur(18px);
  backdrop-filter: blur(18px);
  background: rgb(255 255 255 / 25%);
  

  &__image {
    @include size(42px);
    padding: 0;
    margin: 0;
  }

  &__container {
    padding: 0;
    margin: 0;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: #fff;
    padding: 0;
    margin: 0;
  }

  &--mini-text {
    #{$origin}__text {
      font-size: 17px;
    }
  }
}
