.button-accordion-icon {
  $origin: &;

  @include flex-center;

  flex-direction: row;
  position: relative;
  z-index: 1;

  &__container {
    @include flex-center;
    @include size(56px);
    border: 1px solid var(--primary);
    border-radius: 100px;
    box-shadow: 0 3px 4px 0 rgb(31 33 29 / 11%);
    background: var(--text-talee-white);
    cursor: pointer;
    transition: all var(--transition-time);

    &--active {
      transform: scale(1.2) !important;
      box-shadow: 0 0 25px -5px var(--accent) !important;

      & > #{$origin}__image {
        //
      }
    }

    &:hover {
      transform: scale(1.1);
      box-shadow: 0 5px 10px 0 rgb(31 33 29 / 11%);
    }
  }

  &__pop-up {
    width: max-content;
    position: fixed;
    right: calc(100% + 20px);
    transform-origin: center right;

    display: flex;
    gap: 20px;

    transition: all var(--transition-time);
    z-index: 2;

    background: rgba(255, 255, 255, 0.1) !important;
    -webkit-backdrop-filter: blur(8px) !important;
    backdrop-filter: blur(8px) !important;

    &--hidden {
      @include no-event;
      opacity: 0;
      transform: scale(0);
    }
  }
}
