.card-easy-5 {
  $origin: &;

  padding: 24px;
  display: flex;
  gap: 20px;

  &__image {
    @include size(100px);
    @include min-size(100px);
    object-fit: contain;
    filter: drop-shadow(0 0 14px rgb(180 174 167 / 19%));
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-tale-title);
    margin-bottom: 8px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--text-talee-light);
    margin-bottom: 16px;
  }

  &__block {
    & p {
      font-size: 24px;
      line-height: 130%;
      color: var(--text-talee-dark);
    }

    & b {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 36px;
      line-height: 110%;
      color: var(--text-talee-accent);
    }
  }
}
