.button {
  $origin: &;

  min-height: 56px;
  width: 100%;

  @include flex-center;

  gap: 10px;
  padding: 0 26px;

  background: var(--accent);
  border: 1px solid var(--white);
  color: var(--white);
  border-radius: 45px;

  box-shadow: 0 3px 4px 0 rgb(31 33 29 / 11%);

  text-wrap: nowrap;
  text-decoration: none;
  cursor: pointer;

  transition: all var(--transition-time);

  &__text {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 100%;
    letter-spacing: 0.01em;
    text-align: center;
  }

  &__image {
    display: inline-block;
  }

  &__icon {
    & svg {
      @include size(14px);
    }

    & > * * {
      stroke: var(--white);
    }
  }

  // Effects

  &--light {
    background: var(--white);
    border: 1px solid var(--accent);
    color: var(--accent);

    & * * {
      stroke: var(--accent);
    }
  }

  &--blue {
    background: #399df5;
    border: 1px solid var(--white);
    color: var(--white);

    #{$origin}__image {
      filter: drop-shadow(0 0 2px #399df5) drop-shadow(0 0 4px #399df5);
    }
  }

  &--phone {
    justify-content: flex-end;
    flex-direction: row-reverse;
    padding-inline: 10% 25%;
    border: 2px solid var(--primary);
    box-shadow: 0 3px 4px 0 rgb(31 33 29 / 11%);
    background: var(--text-talee-white);
    gap: 10%;

    #{$origin}__text {
      @include flex-center;
      gap: 5px;

      font-family: var(--font-family);
      font-weight: 500;
      font-size: 23px;
      letter-spacing: 0.01em;
      color: var(--text-talee-dark);
    }

    &::after {
      content: "Або Ви можете одразу зателофонувати до нас ";
      display: inline-block;
      text-wrap: wrap;
      text-align: start;

      font-family: var(--second-family);
      font-weight: 400;
      font-size: 15px;
      line-height: 100%;
      color: var(--text-talee-dark);
      max-width: 170px;
    }
  }

  &--phone-mini {
    position: relative;
    justify-content: center;
    flex-direction: row-reverse;
    border: 2px solid var(--primary);
    box-shadow: 0 3px 4px 0 rgb(31 33 29 / 11%);
    background: var(--text-talee-white);
    gap: 10%;

    #{$origin}__text {
      @include flex-center;
      gap: 5px;

      font-family: var(--font-family);
      font-weight: 500;
      font-size: 23px;
      letter-spacing: 0.01em;
      color: var(--text-talee-dark);
    }

    &::after {
      position: absolute;
      content: "Або Ви можете одразу зателофонувати до нас ";
      bottom: 100%;
      display: inline-block;
      text-wrap: wrap;
      text-align: start;
      padding-block: 16px;

      font-family: var(--second-family);
      font-weight: 400;
      font-size: 15px;
      line-height: 100%;
      color: var(--text-talee-dark);
    }
  }

  &--small {
    min-height: 36px;

    #{$origin}__text {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 80%;
      letter-spacing: 0.01em;
      text-transform: uppercase;
      text-align: center;
    }

    #{$origin}__icon {
      & svg {
        @include size(10px);
        transform: translateY(-1px);
      }
    }
  }

  &--nowrap {
    width: max-content;
    text-wrap: nowrap;
  }

  @include onLabtop {
    &:hover {
      background-color: var(--white);
      border-color: var(--accent);
      color: var(--accent);

      & * * {
        stroke: var(--accent);
      }
    }

    &--light:hover {
      background: var(--accent);
      border-color: var(--white);
      color: var(--white);

      & * * {
        stroke: var(--white);
      }
    }

    &--blue:hover {
      background: var(--white);
      border-color: #399df5;
      color: #399df5;

      #{$origin}__icon {
        & svg {
          @include size(14px);
        }

        & > * * {
          stroke: #399df5;
        }
      }
    }

    &--phone:hover {
      filter: drop-shadow(0 0 15px var(--primary-30p));
    }

    &--phone-mini:hover {
      filter: drop-shadow(0 0 15px var(--primary-30p));
    }
  }
}
