.section-bonus {
  &__title {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 60px;
  }

  @include toLabtop {
    &__list {
      gap: 25px;
    }
  }

  @include toTablet {
    &__list {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  @include toPhone {
    &__title {
      margin-bottom: 30px;
    }

    &__list {
      grid-template-columns: repeat(1, 1fr);
    }

    &__item {
      min-height: 300px;
      max-width: 300px;
      margin: 0 auto;

      & > * {
        height: 100% !important;
      }
    }
  }
}
