.section-policy {
  &__header {
    &__title {
      font-size: 45px;
      line-height: 125%;
      margin-bottom: 35px;
    }

    &__text {
      display: flex;
      flex-direction: column;
      gap: 25px;
      font-size: 20px;
      line-height: 24px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 35px;
  }

  &__block {
    display: flex;
    flex-direction: column;
    gap: 25px;
    font-size: 20px;
    line-height: 24px;

    &__title {
      font-size: 32px;
      line-height: 38.4px;
    }

    &__item {
    }
  }

  &__footer {
    display: flex;
    flex-direction: column;
    gap: 25px;
    font-size: 20px;
    line-height: 24px;

    &__title {
      font-size: 32px;
      line-height: 38.4px;
    }
  }

  &__decor {
    @include size(154px);
    border-radius: 100%;
    float: right;
  }

  @include toLabtop {
    &__header {
      &__title {
        font-size: 36px;
      }
    }

    &__block {
      &__title {
        font-size: 26px;
      }
    }
  }

  @include toPhone {
    &__header {
      &__title {
        font-size: 26px;
      }

      &__text {
        font-size: 16px;
      }
    }

    &__block {
      &__title {
        font-size: 20px;
      }

      &__item {
        font-size: 16px;
      }
    }
    
    &__footer {
      font-size: 16px;

      &__title {
        font-size: 24px;
      }
    }

    &__decor {
      @include size(64px);
    }
  }
}
