.video-link {
  @include size(100%);

  $origin: &;

  &__button {
    @include size(56px);

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    outline: none;
    border-radius: 100%;
    border: 1px solid #ffffff80;
    background-color: #fff3;
    color: white;

    transition: all var(--transition-time);
    z-index: 2;

    -webkit-backdrop-filter: blur(6px);
    backdrop-filter: blur(6px);

    &::after {
      content: "";
      display: block;
      width: 16px;
      height: 19px;
      background-image: url("../images/icons/play.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;

      position: absolute;
      top: 50%;
      left: 53%;

      transform: translate(-50%, -50%);
    }
  }

  &__image {
    @include size(100%);
    display: block;
    object-fit: cover;

    z-index: 1;

    transition: all var(--transition-time);
  }

  &__view {
    @include size(100%);
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0 3px 4px 0 rgb(31 32 29 / 2.5%);
    cursor: pointer;

    transition: all var(--transition-time);

    @include hover {
      transform: scale(1.02);
      box-shadow: 0 6px 8px 2px #1f211d3c;

      #{$origin}__button {
        transform: translate(-50%, -50%) scale(1.05);
        background-color: #fff6;
        box-shadow: 0 0 20px 0 rgb(255 255 255 / 25%);
        z-index: 2;
      }

      #{$origin}__image {
        filter: blur(1px) brightness(85%);
        -webkit-filter: blur(1px) brightness(85%);
        z-index: 1;
        transform: scale(1.025);
      }
    }

    &:active {
      cursor: progress;
    }
  }
}
