.card-block-7 {
  $origin: &;

  position: relative;
  padding: 44px;

  box-shadow: 0 0 14px 0 rgb(180 174 167 / 19%);
  background: var(--surface-white);
  border-radius: 20px;

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    margin-bottom: 8px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 22px;
    line-height: 120%;
    color: var(--text-talee-light);

    @include text-ellipsis-lines;

    margin-bottom: 24px;
  }

  &__block {
  }
}
