.card-easy-3 {
  position: relative;
  padding: 26px 64px;

  &__container {
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 15px;
    margin-bottom: 15px;
  }

  &__number {
    position: absolute;
    top: 26px;
    right: 64px;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
    text-align: center;
    color: var(--primary-color-normal);
  }

  &__image {
    @include min-size(96px);
    @include size(96px);
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-tale-title);
  }

  &__block {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  & hr {
    border-color: rgb(234 133 41 / 50%);
    border-width: 1px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 12px;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--text-talee-light);

    & b {
      font-weight: 900;
    }

    & > li {
      display: flex;

      &::before {
        @include min-size(10px);
        @include size(10px);
        content: "";
        display: block;
        border-radius: 100%;
        background-color: var(--primary);
        margin-top: 8px;
        margin-right: 12px;
      }
    }
  }
}
