.card-doc {
  margin: 1px;

  &__title {
    margin-bottom: 0;

    padding: 24px;
    border-radius: 26px;
    background: var(--primary-surface-white-hover);

    text-align: center;

    font-size: 24px;
    line-height: 130%;

    font-family: var(--second-family);
    color: var(--text-talee-dark);

    & b {
      text-transform: uppercase;
      color: var(--text-talee-accent);
      font-family: var(--font-family);
      font-weight: 700;
    }

    & i {
      color: var(--text-talee-accent);
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding: 24px;
  }

  &__compare {
    display: flex;
    justify-content: space-between;

    &__item {
      @include flex-center;
      flex-direction: column;
      gap: 16px;

      font-family: var(--second-family);
      font-weight: 400;
      font-size: 22px;
      line-height: 120%;
      text-align: center;
      color: var(--text-talee-light);

      &.active {
        color: var(--text-talee-accent);
        font-weight: 700;
        line-height: 130%;
      }
    }
    
    & img {
      border-radius: 20px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__item {
    display: flex;
    gap: 8px;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: 22px;
    line-height: 120%;
    color: var(--text-talee-dark);

    &__title {
      color: var(--text-talee-light);
      opacity: .5;
    }
  }
}
