.card-block-4 {
  $origin: &;
  @include flex-center;
  justify-content: start;

  padding: 50px 60px;
  border-radius: 30px;
  -webkit-backdrop-filter: blur(7px);
  backdrop-filter: blur(7px);
  box-shadow: 0 0 18px 0 rgba(180, 174, 167, 0.2);
  background: rgba(255, 255, 255, 0.65);

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 23px;
    line-height: 123%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    text-align: right;
    color: #ea8529;

    margin-bottom: 15px;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 110%;
    text-align: right;
    color: #62500f;
  }
}
