.title-main {
  $origin: &;
  display: flex;
  flex-direction: column;
  gap: 2px;

  & > span {
    &:first-child {
      place-self: end;
    }

    display: inline-block;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 54px;
    line-height: 113%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #62500f;
  }

  & b {
    font-weight: 400;
    border: 1px solid var(--primary);
    border-radius: 100px;
    padding-inline: 10px;
    margin-inline: -10px;
  }

  & strong {
    font-weight: 500;
  }

  & i {
    color: #ea8529;
    font-style: normal;
  }

  &--light {
    & > span {
      color: var(--primary-surface-white);
    }

    & b {
      border: 1px solid var(--primary-surface-white);
    }
  }

  &--small {
    & > span {
      font-size: 36px;
    }
  }

  &--first {
    & > span {
      &:first-child {
        place-self: start;
      }
    }
  }
}
