.section-map {
  &__title-container {
    margin-bottom: 20px;
  }

  &__content-container {
    position: relative;
    display: grid;
    height: calc(100dvh - 100px);
    max-height: 920px;
  }

  &__menu {
    position: absolute;
    top: 0;
    left: 0;
    place-self: left;
    z-index: 2;
  
    width: 764px;
    height: 100%;

    transition: var(--transition-time);

    &--hidden {
      @include no-event;
      opacity: 0;
      transform: translateX(-100px);
    }
  }

  &__map {
    position: absolute;
    place-self: center;
  }
}
