.card-info-doc {
  display: flex;
  flex-direction: column;
  gap: 20px;

  &__container {
    display: grid;
    grid-template-columns: 2fr 40px 1fr;
  }

  &__line {
    width: 1px;
    height: 100%;
    margin: auto;

    background: var(--text-tale-title);
    opacity: 0.15;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    margin-bottom: 8px;
  }

  & p {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--text-talee-light);

    margin-bottom: 20px;
  }
}
