.card-block-6 {
  $origin: &;

  position: relative;
  padding: 32px;

  &::after {
    // content: "05.02.2024";
    content: attr(data-content);
    display: block;
    position: absolute;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--primary-surface-white);
    z-index: 2;

    top: 32px + 258;
    left: 32px + 20 + 30;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;

    @include size(24px);
    background-image: url("../images/icons/calendar.svg");

    top: 32px + 256;
    left: 32px + 20;
  }

  &__image {
    border-radius: 20px;
    overflow: hidden;
    width: 100%;

    height: 298px;
    object-fit: cover;

    margin-bottom: 20px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    margin-bottom: 12px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 22px;
    line-height: 120%;
    color: var(--text-talee-light);

    @include text-ellipsis-lines;

    margin-bottom: 20px;
  }

  &__block {
  }
}
