.checkbox {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;

  &__text {
    -webkit-user-select: none;
    user-select: none;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 15px;
    line-height: 120%;
    color: var(--text-tale-title-50);

    & > b {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 15px;
      line-height: 120%;
      color: var(--text-tale-title);
    }
  }

  &__real {
    display: none;
  }

  &__fake {
    @include size(20px);

    display: inline-block;
    position: relative;

    border: 1px solid gray;
    border-radius: 2px;
    transition: var(--transition-time);

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */

    &::before {
      @include flex-center;
      @include size(118%);
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0);

      border-radius: 2px;
      background: var(--text-tale-title);

      opacity: 0;
      transition: var(--transition-time);
    }

    &::after {
      @include flex-center;
      content: ""; // ✔
      width: 14px;
      height: 10px;

      background-image: url("../images/icons/checkbox.svg");
      background-repeat: no-repeat;
      background-size: contain;

      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0);

      font-size: 75.5%;
      color: white;

      opacity: 0;
      transition: var(--transition-time);
    }

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */

    &:hover {
      border: 1px solid var(--primary);

      &::before {
        background: var(--primary);
      }

      &::after {
      }
    }

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */

    &:active {
      &::before {
        background: var(--primary-material-500);
      }

      &::after {
      }
    }
  }
}

.checkbox__real:checked + .checkbox__fake::before,
.checkbox__real:checked + .checkbox__fake::after {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}
