
.menu {
  @include size(100%);
  $origin: &;
  position: relative;
  overflow: hidden;

  display: grid;
  grid-template-rows: 1fr max-content;

  border: 2px solid var(--surface-white-30);
  border-radius: 20px;
  
  background: rgba(255, 255, 255, 0.8) !important;
  -webkit-backdrop-filter: blur(8px) !important;
  backdrop-filter: blur(8px) !important;
  box-shadow: 0 0 14px 0 rgba(180, 174, 167, 0.08);
  z-index: 10;

  // filter: blur(8px);

  &__container {
    display: grid;
    grid-template-rows: max-content 1fr;
    overflow: hidden;
  }

  &__head {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    padding: 40px;
    padding-bottom: 0;
    z-index: 1;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow-y: scroll;
    padding-left: 40px;
    margin-right: 10px;
    padding-right: 10px;

    @include size-scrollbar(20px);
  }

  &__content {
    @include flex-center;
    flex-direction: column;
    position: relative;
    margin-bottom: 2px;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    padding: 40px;
    padding-bottom: 0;
  }

  padding-bottom: 40px;

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 48px;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-accent);
  }

  &--list-grid {
    #{$origin}__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }
}
