.card-block-3 {
  $origin: &;

  display: flex;
  flex-direction: row;
  gap: 20px;
  border-radius: 30px;

  padding: 8px;
  -webkit-backdrop-filter: blur(7px);
  backdrop-filter: blur(7px);
  box-shadow: 0 0 18px 0 rgba(180, 174, 167, 0.2);
  background: rgba(255, 255, 255, 0.58);


  &__image {
    border: 2px solid rgba(234, 133, 41, 0.25) !important;
    border-radius: 30px;
    min-width: 296px;
    min-height: 308px;
    object-fit: cover;
  }

  &__container {
    display: grid;
    grid-template-rows: max-content max-content 1fr;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 23px;
    line-height: 123%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #ea8529;

    margin-bottom: 15px;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 110%;
    color: #62500f;
    margin-bottom: 20px;
  }

  &__block {
    display: flex;
    flex-direction: column;

    justify-content: space-between;
  }
}
