.card-hard {
  max-height: 400px;

  &__head {
    display: flex;
    justify-content: space-between;
    padding: 40px;
    padding-bottom: 0;
    margin-bottom: 24px;

    &-container {
      display: flex;
      gap: 24px;
    }
  }

  &__image {
    @include min-size(102px);
    @include size(102px);
    position: relative;
    overflow: hidden;
    border: 2px solid var(--primary);
    border-radius: 100%;

    & img {
      @include size(108%);
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 28px;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    margin-bottom: 8px;
  }

  &__info {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: var(--text-talee-light);
  }

  &__body {
    padding: 40px;
    padding-top: 0;
    padding-right: 0;

    &-container {
      display: flex;
      justify-content: space-between;
      padding-right: 40px;

      margin-bottom: 8px;
    }
  }

  &__subtitle {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-talee-light);
  }

  &__date {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    text-align: right;
    color: var(--text-talee-light);
  }

  &__text {
    max-height: 100px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    text-align: justify;
    color: var(--text-talee-dark);

    padding-right: 10px;
    margin-right: 10px;

    overflow-y: auto;

    @include size-scrollbar(20px);
  }
}
