.button-accordion-simple {
  $origin: &;

  @include flex-center;

  flex-direction: column;
  position: relative;
  z-index: 2;
  cursor: pointer;

  @include hover {
    filter: drop-shadow(0 0 10px var(--surface-white-80));
  }

  &__container {
    @include flex-center;
    @include size(56px);
    position: relative;
    border-radius: 100%;
    background-color: var(--surface-white-80);
    box-shadow: 0 0 4px 0 rgb(0 0 0 / 21%);
    z-index: 2;

    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);

    &::after {
      content: "";
      display: block;
      position: absolute;
      background-color: #b4aea7;
      height: 3px;
      width: 20px;
      border-radius: 2px;
      transition: var(--transition-time);
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      background-color: #b4aea7;
      height: 3px;
      width: 20px;
      border-radius: 2px;
      transition: var(--transition-time);
      transform: rotate(90deg);
    }

    &--active {
      // background: #ea8529;
      //

      &::before {
        transform: rotate(0deg);
      }
    }
  }

  &__pop-up {
    // width: max-content;

    @include shadow;
    position: absolute;
    bottom: 65%;
    right: 65%;
    transform-origin: right bottom;

    width: 100%;
    min-width: 232px;

    // ----
    transition: all var(--transition-time);
    z-index: 2;

    &--hidden {
      @include no-event;
      transform: scale(0);
      opacity: 0;
    }
  }

  &--mini {
    #{$origin}__container {
      @include size(36px);
    }
    #{$origin}__pop-up {
      // padding: 12px 17px 15px;
      min-width: 200px;
    }
  }
}
