.card-easy {
  $origin: &;

  padding: 14px 32px;

  &__container {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
  }

  &__image {
    @include size(64px);
    @include min-size(64px);
    object-fit: contain;
  }
  
  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--text-talee-dark);
    opacity: .8;
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: end;
  }
}
