$main-color: var(--primary);

.splide__arrow svg {
  fill: none;
  height: auto;
  width: auto;
}

.splide {
  $origin: &;

  cursor: grab;

  &:active {
    cursor: grabbing;
  }

  &--default {
    $transform-translate-y: 20px;
    filter: drop-shadow(0 3px 10px rgb(31 33 29 / 14%));
    margin-bottom: 50px;
    height: max-content;

    #{$origin}__slide {
    }
    #{$origin}__track {
      & > * {
      }
    }

    #{$origin}__arrow {
      @include size(40px);
      background-color: var(--white);
      border: 1px solid $main-color;
      top: 100%;
      bottom: inherit;
      transform: translateY($transform-translate-y - 6);
      z-index: 1;

      & > svg {
        width: 6px;
        height: 12px;

        @include size(1.1em);
      }

      & > svg path {
        // fill: $main-color;
        stroke: $main-color;
      }

      transition: background-color 0.1s, box-shadow 0.2s, border 0.2s,
        opacity 0.2s;

      @include onLabtop {
        &:hover {
          opacity: 1;
          box-shadow: 0 0 20px -10px $main-color;
        }

        &:active {
          transform: translateY($transform-translate-y - 6) scale(0.9);
          background-color: $main-color;
          color: var(--white);
          border: 6px solid $main-color;

          & > svg path {
            // fill: var(--white);
            stroke: var(--white);
          }
        }
      }
    }

    #{$origin}__item {
      border-radius: 20px;
      object-fit: contain;
      min-height: 300px;
      background-color: rgb(128 128 128 / 12.8%);
    }

    #{$origin}__pagination {
      transform: translateY($transform-translate-y);
      gap: 10px;
      bottom: inherit;
      top: 100%;

      &__page {
        @include size(8px);
        border: 1px solid $main-color;
        border-radius: 100%;

        &.is-active {
          background-color: $main-color;
          transform: scale(1.3);
        }
      }
    }
  }

  // ----------

  &--other {
    $transform-translate-y: 20px;
    filter: drop-shadow(0 3px 10px rgb(31 33 29 / 14%));
    margin-bottom: 50px;
    height: max-content;

    #{$origin}__arrows {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      margin-top: 32px;
      cursor: default;
    }

    #{$origin}__arrow {
      @include size(24px);
      @include flex-center;
      position: relative;
      transform: none;
      bottom: inherit;
      top: inherit;
      right: inherit;
      left: inherit;
      border: none;
      border-radius: 100%;
      background: transparent;
      background-color: transparent;

      transition: all var(--transition-time);

      & > svg {
        width: 6px;
        height: 12px;

        @include size(1.1em);
      }

      & > svg path {
        stroke: #7c7976;
      }

      @include onLabtop {
        &:hover {
          transform: scale(1.1);

          & > svg path {
            stroke: $main-color;
          }
        }

        &:active {
          transform: scale(0.8);
          background-color: transparent;
          color: $main-color;
          border: none;

          & > svg path {
            stroke: $main-color;
          }
        }
      }
    }

    #{$origin}__pagination {
      @include flex-center;
      gap: 12px;
      position: relative;
      width: max-content;
      transform: none;
      bottom: inherit;
      top: inherit;
      right: inherit;
      left: inherit;

      &__page {
        background-color: var(--neutral-color-20);
        border: none;
        transition: all var(transition-time);

        &.is-active {
          width: 15px;
          height: 17px;
          background-color: transparent;
          border: none;
          border-radius: 0;
          transform: none;
          background-image: url("../images/icons/slider_point_active.png");
          background-size: contain;
          filter: drop-shadow(0 0 6px rgb(0 0 0 / 20%));
        }
      }
    }
  }
}
