.card-easy-4 {
  $origin: &;
  display: flex;
  gap: 20px;
  padding: 36px 32px;

  &__container {
    display: flex;
    flex-direction: column;

    // margin-bottom: 20px;
  }

  &__image {
    @include size(64px);
    @include min-size(64px);
    object-fit: contain;
    margin-top: 12px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 28px;
    line-height: 130%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: var(--text-tale-title);
    margin-bottom: 20px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--text-talee-dark);
    opacity: 0.8;
    margin-bottom: 15px;
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: start;
  }
}
