* { box-sizing: border-box }

::selection {
  background-color: var(--light-blue-50p);
  color: var(--white);
}

::target-text {
  color: var(--primary);
  background-color: #fbe7d4;
  border-radius: 10px;
  display: inline-block;
}

hr { border-color: var(--text-tale-title-20) }

a { /* (love hate) LVHA (link visited hover active) */
  $color: black;
  color: $color;
  text-decoration: none;

  &:link { color: $color }
  &:visited { color: $color }
  &:hover { /* text-decoration: underline */ }
  &:active { color: $color }
}

._link {
  &:hover {
    text-decoration: underline;
  }
}

._no-event { @include no-event }
._relative { position: relative }
._absolute { position: absolute }


