.card-block-1 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding: 24px 32px;
  border: 2px solid var(--surface-white-30);
  border-radius: 20px;

  -webkit-backdrop-filter: blur(18px);
  backdrop-filter: blur(18px);
  background: rgb(255 255 255 / 25%);

  &__image {
    @include size(100px);
    margin-bottom: 24px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 28px;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--surface-white-80);

    margin-bottom: 8px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    color: var(--surface-white-80);

    margin-bottom: 8px;
  }
}
