// /* Page */

.page {
  scroll-behavior: smooth;
  scroll-padding-top: 100px;
  height: 100%;

  color: var(--text);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 110%;

  &__body {
    width: 100%;
    height: 100%;
    margin: 0 auto;

    position: relative;
    background-color: var(--bg);
    transition: color var(--transition-time),
      background-color var(--transition-time);

    @include toLabtop {
      transition: all var(--transition-time);
    }

    &--stop-scroll {
      overflow: hidden;

      padding-right: var(--scroll-bar-size);
    }
  }

  &__wrapper {
    width: 100%;
    max-width: 1920px;
    min-height: 100dvh;
    margin: auto;
    display: flex;
    flex-direction: column;
  }

  &__header {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;

    z-index: 100;
  }

  &__menu {
    @include no-event;

    position: sticky;
    bottom: 0;
  
    display: flex;
    justify-content: end;
  
    overflow: hidden;
    z-index: 10;

    height: 100dvh;
    margin-top: -100dvh;

    &__container {
      @include flex-center;

      perspective: 200px;
      flex-direction: column-reverse;
      gap: 20px;
      place-self: flex-end;
      pointer-events: all;
    }

    margin-bottom: -160px;
    padding-right: 50px;
    padding-bottom: 50px;

    @include toDesktop {
      margin-bottom: -132px;
      padding-right: 20px;
      padding-bottom: 20px;
    }

    @include toPhone {
      margin-bottom: -262px;
    }
  }

  &__main {
    flex: 1 1 auto;

    display: flex;
    flex-direction: column;
    gap: 60px;

    padding-inline: 30px;

    min-height: 100%;

    margin-bottom: 162px;

    @supports (overflow: clip) {
      overflow: clip;
    }

    @supports not (overflow: clip) {
      overflow-x: hidden;
    }

    position: relative;

    &::before {
      @include fullscreen;
      @include no-event;
      content: "";
      box-shadow: inset 0 0 42px 8px var(--bg);
      z-index: 1;
    }

    @include toDesktop {
      margin-bottom: 132px;
      padding: 0 20px;
    }

    @include toPhone {
      margin-bottom: 262px;
      padding: 0 15px;
    }
  }
}
