.main-photo {
  $origin: &;

  & * {
    @include flex-center;
    flex-direction: column;
    box-sizing: content-box;
    border-radius: 100%;
  }

  &__block {
    position: absolute;
    border-radius: 0;
    
    & * {
      border-radius: 0;
    }
  }

  &__0 {
    @include size(100%);
    position: relative;
    background-color: rgb(180 174 167 / 6%);
  }

  &__1 {
    @include size(85%);
    background-color: rgb(180 174 167 / 6%);
  }

  &__2 {
    @include size(80%);
    background-color: rgb(180 174 167 / 6%);
  }

  &__3 {
    @include size(75%);
    background-color: rgb(98 80 15 / 2%);
  }

  &--blue {
    #{$origin}__0 {
      background-color: rgb(41 147 221 / 6%);
    }
    
    #{$origin}__1 {
      @include size(85%);
      background-color: rgb(41 147 221 / 6%);
    }
    
    #{$origin}__2 {
      @include size(80%);
      background-color: rgb(41 147 221 / 6%);
    }
    
    #{$origin}__3 {
      @include size(75%);
      background-color: rgb(41 147 221 / 2%);
    }
  }

}
