.pop-up {
  $origin: &;
  position: fixed;
  inset: 0;
  z-index: 102;
  overflow-y: auto;

  display: flex;
  flex-direction: column;
  height: 100dvh;

  transition: .5s;

  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  background: rgba(70, 67, 67, 0.56);

  &--closed {
    opacity: 0;
    -webkit-backdrop-filter: blur(0);
    backdrop-filter: blur(0);
    background: transparent;
  }

  &__button {
    position: sticky;

    right: 0;
    top: 0;
    display: flex;
    justify-content: flex-end;
    
    margin: 30px;
    z-index: 10;
    
    @include toPhone {
      margin: 10px;
    }

    & > button {
      @include size(58px);
    }
  }

  &__content {
    position: relative;
    padding: 60px;
  }

  &--fixed {
    overflow: hidden;

    #{$origin}__button {
      position: absolute;
    }

    #{$origin}__content {
      overflow: hidden;
      height: 100%;
      padding: 60px;
    }
  }
}
