.card-block-5 {
  $origin: &;

  display: flex;
  align-items: center;
  gap: 32px;
  padding: 30px 24px;

  &__image {
    @include min-size(140px);
    @include size(140px);
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    margin-bottom: 8px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--text-talee-light);

    @include text-ellipsis-lines;

    margin-bottom: 20px;
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: end;
  }

  &--mini {
    align-items: start;
    height: 230px;
    // height: 100%;
    
    #{$origin}__text {
      @include text-ellipsis-lines(4);
      margin-bottom: 15px;
    }

    #{$origin}__image {
      @include size(100px);
      @include min-size(100px);
      border-radius: 100%;
      box-shadow: 0 0 14px 0 rgba(180, 174, 167, 0.19);
    }

    #{$origin}__container {
      height: 100%;
      display: grid;
      grid-template-rows: max-content 1fr max-content;
    }
  }
}
