.social-link {
  @include size(68px);

  @include onLabtop {
    transition: all var(--transition-time);

    &:hover {
      transform: scale(1.1);
      filter: drop-shadow(0 6px 12px #1f211d20) 
    }
  }

  &--small {
    @include size(52px);
  }

  @include toPhone {
    @include size(46px);
  }
}
