}

.decor-circle-info {
  @include size(200px);
  @include flex-center;

  position: absolute;
  top: 40px;
  left: -150px;

  background-color: var(--primary-surface-white);
  border: 1px solid var(--primary-material-100);
  border-radius: 100%;

  &--blue {
    border: 1px solid var(--second-color-light-active);
  }

  &__container {
    @include flex-center;
    flex-direction: column;
    transform: rotate(-30deg);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--neutral-color-30);
  }
}

.object-photo {
  position: relative;

  &__link {
    position: absolute;
    display: block;
    left: -10%;
    bottom: 20%;
    padding-right: 40px;
    padding-bottom: 4px;
    border-bottom: 1px solid hsl(47deg 73% 22% / 50%);
    cursor: pointer;
    z-index: 1;

    &::after {
      @include no-event;
      content: "";
      display: block;
      position: absolute;
      border-bottom: 1px solid hsl(47deg 73% 22% / 50%);
      width: 50px;
      transform-origin: left bottom;
      left: 100%;
      bottom: -1px;
      transform: rotate(-30deg);
      transition: all var(--transition-time);
    }

    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 80%;
    text-transform: uppercase;
    color: #62500f !important;

    transition: all var(--transition-time);

    @include hover {
      color: var(--primary) !important;
      border-bottom: 1px solid var(--primary-50p);

      &::after {
        border-bottom: 1px solid var(--primary-50p);
      }
    }
  }

  & > img {
    @include size(570px);
    position: relative;
    object-fit: contain;
    filter: drop-shadow(0 90px 50px rgb(16 15 14 / 17%));

    animation-name: anim-shlem-position;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }

  & > .decor-blur-point {
    position: absolute;
    bottom: 0;
    background-color: rgb(0 0 0 / 65%) !important;

    animation-name: anim-shadow-under-shlem;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
}

@keyframes anim-shlem-position {
  from {
    transform: translateY(20px);
  }

  to {
    transform: translateY(40px);
  }
}

@keyframes anim-shadow-under-shlem {
  from {
    opacity: 0.3;
  }

  to {
    opacity: 0.8;
    width: 458px;
    height: 18px;
  }
}

