  }

  &__brow {
    position: absolute;
    top: -16px;
    left: 20%;

    &--right {
      left: auto;
      right: 20%;
      transform: scaleX(-1);
    }
  }
}

.qwert {
  @include flex-center;
  position: relative;

  &__line {
    position: absolute;
    border-bottom: 2px dashed #b0a583;
    width: 944px;
  }

  &__container {
    position: relative;
    width: max-content;
    height: max-content;
    border: 2px dashed rgb(176 165 131 / 50%);
    border-radius: 100%;
    padding: 30px;
    z-index: 1;
    background-color: var(--bg);
  }

  &__image {
    overflow: hidden;
    border-radius: 100%;
  }
}

.anim-rotate {
  animation-name: anim-rotate;
  animation-duration: 200s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;

  &--reverse {
    animation-name: anim-rotate;
    animation-duration: 100s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: reverse;
  }

  &--stop {
    animation-name: anim-rotate;
    animation-duration: 200s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
}

@keyframes anim-rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.h2 {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 36px;
  line-height: 110%;
  text-transform: uppercase;
  text-align: center;
  color: #62500f;

  & > b {
    color: #ea8529;
  }
}

.piker {
  $origin: &;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  filter: drop-shadow(0 0 5px rgb(0 0 0 / 15.8%));
  cursor: pointer;

  &__border {
    min-width: 140px;
    min-height: 140px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid;
    border-radius: 100%;
    transition: all var(--transition-time);

    &--hidden {
      border-color: transparent !important;
    }
  }

  &__color {
    min-width: 90px;
    min-height: 90px;
    border-radius: 100%;
    transition: all var(--transition-time);
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 110%;
    text-align: center;
    color: #5d5955;
  }

  @include hover {
    #{$origin}__color {
      transform: scale(1.2);
    }
  }
}

.gradient-fon {
  @include size(100%);
  position: absolute;
  filter: blur(300px);
  z-index: -1;

  &--beige {
    background: linear-gradient(
      135deg,
      rgb(255 249 247 / 60%) 0%,
      rgb(239 203 184 / 60%) 56.13%
    );
  }

  &--orange {
    background: linear-gradient(
      135deg,
      rgb(255 251 247 / 60%) 0%,
      rgb(251 204 161 / 60%) 56.13%
    );
  }

  &--green {
    background: linear-gradient(
      135deg,
      rgb(253 255 247 / 60%) 0%,
      rgb(223 239 184 / 60%) 56.13%
    );
  }

  &--yellow {
    background: linear-gradient(
      135deg,
      rgb(255 255 247 / 60%) 0%,
      rgb(239 237 177 / 60%) 56.13%
    );
  }

  &--pink {
    background: linear-gradient(
      135deg,
      rgb(255 247 247 / 60%) 0%,
      rgb(239 189 184 / 60%) 56.13%
    );
  }

  &--white {
    background: linear-gradient(
      135deg,
      rgb(255 255 255 / 60%) 0%,
      rgb(232 232 232 / 60%) 56.13%
    );
  }
}

.textarea {
  resize: none;
  display: block;
  width: 100%;
  padding: 0 45px;
  margin: 26px 0;
  background: transparent;

  &:focus {
    outline: none;
  }

  &::placeholder {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: var(--text-talee-dark);
    opacity: 0.5;
  }
}
