.input-new {
  position: relative;
  display: flex;
  width: 100%;
  justify-content: start;
  align-items: center;

  &__input {
    display: block;
    width: 100%;
    padding: 26px 45px;
    background: transparent;

    &:focus {
      outline: none;
    }

    &::placeholder {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 18px;
      line-height: 130%;
      color: var(--text-talee-dark);
      opacity: 0.5;
    }
  }

  &--search {
    &::before {
      @include size(22px);
      content: "";
      display: inline-block;
      cursor: pointer;
      background-image: url("../images/icons/search_.svg");
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      transform: translate(26px, -2px);

      transition: all var(--transition-time);
    }

    @include hover {
      &::before {
        transform: translate(26px, -2px) scale(1.2);
      }
    }
  }
}

