.section-modal {
  @include flex-center;
  height: 100%;
  padding: 30px;

  &__container {
    position: relative;
    height: 100%;
    width: 100%;
    padding: 20px;
  }

  &__bg {
    @include fullscreen;
    background-color: var(--primary-30p);
    filter: blur(200px);
  }

  &__screen {
    @include flex-center;
    height: 100%;
  }

  &__content {
    max-height: 100%;
    max-width: 100%;
    border-radius: 20px;
    z-index: 1;
    filter: drop-shadow(0 0 40px var(--primary-30p));
  }
}
