.button-icon-service {
  @include flex-center;
  @include min-size(56px);
  @include max-size(56px);
  place-self: center;

  padding: 10px;
  border-radius: 100%;
  border: 1px solid var(--white);
  box-shadow: 0 3px 4px 0 rgb(31 33 29 / 11%);
  background-color: var(--primary);
  cursor: pointer;
  transition: all var(--transition-time);

  @include hover {
    transform: scale(1.05);
  }
}
