.header {
  @include gridSetting;

  $origin: &;

  align-items: center;
  border: 1px solid rgb(180 174 167 / 21%);
  border-radius: 0 0 18px 18px;
  padding: 0 30px;

  // box-shadow: 0 4px 10px 0 #96b6dc4d;
  // background: #ffffffe6;

  // -webkit-backdrop-filter: blur(16px);
  // backdrop-filter: blur(16px);

  -webkit-backdrop-filter: blur(18px);
  backdrop-filter: blur(18px);
  box-shadow: 0 4px 11px 0 rgb(180 174 167 / 15%);
  background: rgb(255 253 248 / 80%);

  &__logo {
    // grid-column: 1/1;
  }

  &__container {
    height: 100%;

    // grid-column: 2/5;
    display: flex;
    align-items: center;
    gap: clamp(100px, 3vw, 10%);
  }

  &__navigation {
    width: 45%;
  }

  &__button {
    // grid-column: 5/7;
    justify-self: end;
    max-width: 214px;
    width: 100%;
  }

  &__slide-menu {
    // display: none;
  }

  &__link {
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    &::before {
      content: "";
      display: block;
      height: 4px;
      background-color: var(--light-blue);
      border-radius: 6px;

      width: 0%;
      position: absolute;
      bottom: 0;
      left: 0;

      transition: width var(--transition-time);
    }

    &--active::before {
      content: "";
      width: 100%;
    }
  }

  @include toDesktop {
    padding: 0 20px;
    display: flex;
    justify-content: space-between;

    &__logo {
      width: 184px;
      padding: 0;
    }

    &__container {
      justify-content: space-around;
      gap: none;
    }
  }

  @include toLabtop {
    height: 70px;

    &__navigation {
      display: none;
    }

    &__slide-menu {
      display: block;
    }
  }

  @include toPhablet {
    &__link {
      display: none;
    }

    &__button {
      display: none;
    }
  }

  @include toPhone {
    padding: 0 15px;

    &__logo {
      width: 140px;
    }
  }
}
