.accordion {
  padding: 32px;
  overflow: hidden;
  height: max-content;

  &__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;

    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: 22px;
    line-height: 120%;
    color: var(--text-tale-title);
  }

  &__text {
    margin-top: 25px;
    padding-top: 25px;
    border-top: 1px solid rgb(98 80 15 / 30%);

    font-family: var(--second-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    text-align: justify;
    color: var(--text-talee-light);

    transition: all var(--transition-time);

    &--hidden {
      height: 0;
      margin: 0;
      padding: 0;
      opacity: 0;
    }
  }

  &__icon {
    @include flex-center;
    @include min-size(64px);
    border-radius: 100%;
    background-color: #fff8f0;
    box-shadow: 0 0 4px 0 rgb(0 113 155 / 15%);
  }

  &__line {
    position: absolute;
    width: 16px;
    height: 2px;
    border-radius: 2px;
    background-color: var(--primary);
    transition: all var(--transition-time);

    &--plus {
      transform: rotate(-90deg);
    }
  }
}
